body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  scroll-behavior: smooth;
}

body{
  background-color: lightgray;
  float: left;
}
.topNav{
  min-height: 4em;
  max-width: 100vw;
  display: flex;
  align-items: center;
}

.show{
  display: block;
}

.notShow{
  display: none;
}

.sideMenu{
  min-height: 100vh;
  width: 100%;
  padding-top: 6em;
  position: absolute;
  z-index: 10;
}

.content{
  padding-top: 5em;
  max-width: 100vw;
  min-height: 100vh;
  background-color: lightgray;
}

.paper{
  color: rgba(0, 0, 0, 0.50);
  background-color: white;
  margin: .5em 1em;
  padding: .75em;
  border-radius: .35em;
}

.topBut{
  position: -webkit-sticky;
  position: sticky;
  bottom: 1em;
  left: 1em;
}
